$width: 92vmin

html, body
  margin: 0
  padding: 0
  text-align: center
  height: 100%
  overflow: hidden

body
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab)
  background-size: 400% 400%
  animation: gradient 15s ease infinite
  font-family: 'Fira Code', monospace
  font-size: 100%
  position: relative
  *
    -webkit-tap-highlight-color: transparent


.splash
  position: absolute
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  cursor: pointer
  width: 80vmin
  filter: drop-shadow(0px 10px 5px black)
  img
    width: 60vmin
    display: block
    margin: 0 auto

  h3  
    font-size: 8vmin
    margin: 0.2em
  p
    font-size: 4vmin
    margin: 0.2em
  h3,p
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000
    color: white

.canvas
  position: fixed
  top: 0
  left: 0
  bottom: 0
  right:0

.puzzle
  cursor: pointer
  height: $width
  max-height: 700px
  width: $width
  max-width: 700px
  margin: 4vmin auto
  position: relative
  box-shadow: black 1vmin 1vmin 10px 0px
  display: flex
  flex-direction: column
  border: 1px solid black
  touch-action: none
  user-select: none
  .row
    flex: 1 1 0px
    display: flex
    .cell
      flex: 1 1 0px
      border: 1px solid black
      background: white
      font-size: 0rem
      svg
        font-size: 1rem
      text
        text-anchor: middle
        text-transform: uppercase
        font-size: 0.5em
  .marker
    position: absolute
    background: rgba(255,0,0,0.5)
    touch-action: none
    pointer-events: none

.words
  touch-action: none
  pointer-events: none
  span
    display: inline-block
    margin: 0 0.5em
    font-size: 2em
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000
    color: white
    &.found
      text-decoration: line-through

@keyframes gradient
  0%
    background-position: 0% 50%
  50%
    background-position: 100% 50%
  100%
    background-position: 0% 50%
